import React, { ReactElement, useEffect, useRef, useState } from "react";
import { Curtain, useCurtain } from "../contexts/Routes";
import { animate, delay } from "doppio";

interface MainProps {
    children: ReactElement[] | JSX.Element | string;
}

const routes = [
    {
        path: "/",
        name: "Top"
    },
    {
        path: "/news",
        name: "News"
    },
    {
        path: "/menu",
        name: "Menu"
    },
    {
        path: "/blogs",
        name: "Blog"
    },
    {
        path: "/access",
        name: "About"
    }
];

export function Main(props: MainProps) {
    return (
        <>
            <Curtain colors={["#468189", "#77aca2", "#9dbebb", "#c4cdc1", "#f4eace", "#dee1dd"]}>
                <Body {...props} />
            </Curtain>

        </>
    );
}

function Body(props: MainProps) {
    const main = React.useRef<HTMLDivElement | null>(null);
    const { open, close } = useCurtain();

    React.useEffect(() => {
        open();

        if (main.current) {
            animate(main.current, "opacity", "0", 0);
            delay(200).then(() => {
                if (main.current)
                    animate(main.current, "opacity", "1", 1600, 800);
            });
        }
    }, []);

    const assign = async (path: string) => {
        await close();
        location.assign(path);
    }

    return <>
        <header className="d-flex" style={{ height: "88px" }}>
            <title className="mt-3">Home Page</title>
            <div className="flex-grow-1" />
            <div className="d-flex mx-4 my-12">
                {
                    routes.map(route => <a
                        key={route.name}
                        className={route.path === location.pathname ? "mx-2 active" : "mx-2"}
                        onClick={_ => assign(route.path)}
                    >
                        {route.name}
                    </a>)
                }
            </div>
        </header>
        <main ref={main} style={{ opacity: "0", width: "100vw" }}>
            {props.children}
        </main>
        <footer style={{ background: "#101010" }} className="pa-8 text-center mt-12">
            <p style={{ color: "white" }}>©2021 Liange 尾道 All Rights Reserved.</p>
        </footer>
    </>
}
